<template>
    <div class="creating_container">
        <LoaderComponent v-if="loading" />
        <div v-show="gameCode" class="main_container">
            <h6>{{ gameCode }}</h6>
            <div class="btn_container">
                <div @click.stop="copyCode" class="copy btn">
                    <span class="material-symbols-rounded"> content_copy </span>
                </div>
                <div @click.stop="shareCode" class="share btn">
                    <span class="material-symbols-rounded"> share </span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import socketIO from "@/socket";

import LoaderComponent from "@/components/LoaderComponent.vue";

export default {
    beforeUnmount() {
        if (!this.paired) {
            this.socket.emit("erase_game", this.gameCode);
        }
        this.socket.off("paired");
    },
    created() {
        this.socket = socketIO;
        this.requestCode();

        this.socket.on("paired", () => {
            this.paired = true;
            this.$router.replace({ name: "selection" });
        });
    },
    components: {
        LoaderComponent,
    },
    data() {
        return {
            socket: null,
            loading: true,
            gameCode: undefined,
            paired: false,
        };
    },
    methods: {
        async requestCode() {
            this.socket.emit("create_game", (gameCode) => {
                this.gameCode = gameCode;
                this.loading = false;
            });
        },
        copyCode() {
            navigator.clipboard
                .writeText(this.gameCode)
                .then(() => {
                    this.copiedToast(true);
                })
                .catch(() => {
                    this.copiedToast(false);
                });
        },
        copiedToast(state) {
            const oldToast = document
                .getElementById("app")
                .querySelector(".toast");
            if (oldToast) oldToast.remove();

            const toast = document.createElement("div");

            toast.classList.add("toast");

            toast.innerHTML = `
				<span class="material-symbols-rounded toast_icon ${state ? "success" : ""}">${
                state ? "check" : "error"
            }</span>
				<h3 class="toast_text">${state ? "Code Copied!" : "Error"}</h3>
				<div class="toast_timer"></div>
			`;

            document.getElementById("app").appendChild(toast);
            toast.style.left = `${
                document.getElementById("app").offsetWidth / 2 -
                toast.offsetWidth / 2
            }px`;
            setTimeout(() => {
                toast.remove();
            }, 1500);
        },
        shareCode() {
            if (navigator.share) {
                navigator
                    .share({
                        title: "Your Game Link",
                        text: "Directly connect to the game",
                        url: `${window.location.host}/joining?gamecode=${this.gameCode}`,
                    })
                    .catch(() => {
                        window.open(
                            `https://wa.me/?text=` +
                                encodeURIComponent(
                                    `${window.location.host}/joining?gamecode=${this.gameCode}`
                                ),
                            "_blank"
                        );
                    });
            } else {
                window.open(
                    `https://wa.me/?text=` +
                        encodeURIComponent(
                            `${window.location.host}/joining?gamecode=${this.gameCode}`
                        ),
                    "_blank"
                );
            }
        },
    },
    name: "CreatingComponent",
};
</script>
<style lang="scss" scoped>
.creating_container {
    height: 100%;
    width: 100%;

    .main_container {
        align-items: center;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        width: 100%;

        h6 {
            background-color: #151515;
            border-radius: 5px;
            font-size: 1.6em;
            margin: 0 auto 30px;
            max-width: 80%;
            padding: 10px 20px;
            position: relative;
            width: min-content;

            &::after {
                background-color: #151515;
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;
                content: "Your Game Code:";
                font-size: 0.5em;
                left: 50%;
                padding: 5px 10px 0;
                position: absolute;
                top: 0;
                transform: translate(-50%, -70%);
                width: max-content;
            }
            &:hover {
                animation: tada 1.5s linear 1;
            }
        }

        .btn_container {
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: center;
            width: min-content;

            .btn {
                align-items: center;
                background-color: #151515;
                border-radius: 50%;
                cursor: pointer;
                display: flex;
                height: 50px;
                justify-content: center;
                width: 50px;

                &.share {
                    margin-left: 50px;
                }
                &:hover {
                    animation: heartBeat 1s linear infinite;
                }
            }
        }
    }
}
</style>
